import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import MyStack from "./views/MyStack.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/my-stack",
      name: "my-stack",
      component: MyStack
    }
  ]
});
